body{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #10101e;
}

div{
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    background:black;
    font-family: 'Segoe UI', sans-serif;
    width: 340px;
    padding: 15px 10px;
    border: 3px solid #2e94e3;
    font-size: 50px;
    text-shadow: 3px 3px #2e94e3;
}
div:hover:last-child{
  background:#2e94e3;
  box-shadow: 0 0 30px #2e94e3;
}